
html {
  height: 100%;
}
body {
  min-height: 100%;
  background-color: gainsboro;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
  margin: 0px;
}
.navcontainer {
    background-color: cadetblue;
    font-size: 20px;
    text-align: right;
    padding-right: 10px;
}
hr {
    margin: 10px;
    border-color: black;
}
/* ab hier ausgegliedertes styling aus den komponenten */
.hideelement {
  display: none
}
.displayinline {
  display: inline;
}
.headermargintop {
  margin-top: 20px;
}
.gethelp {
  float: right;
  margin: 2%;
  padding: 0px;
  color: orangered;
}
.helptext{
  margin-left: 5px;
  margin-right: 5px;
}
.nopeintext{
  width: 20px;
  height: 20px;
  margin-bottom: 3px;
}
.deleteicon{
  width: 40px;
  height: 40px;
}
.buttoncadetblue{
  background-color: cadetblue;
  border-radius: 10px;
}
.buttoncadetblue:hover {
  cursor: pointer;
  color: lightgrey;
  border-color: lightgrey;
  background-color: lightseagreen;
}
.inputfield {
  border-color: black;
  border-radius: 5px;
}
.submitbutton{
  border-radius: 10px;
  background-color: cadetblue;
}
.submitbutton:hover {
  cursor: pointer;
  color: lightgrey;
  border-color: lightgrey;
  background-color: lightseagreen;
}
.unorderedlist {
  margin-left: 10px;
  margin-top: 20px;
  margin-right: 10px
}
.listitem {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  list-style: none;
  text-align: left;
}
.listitem:hover {
  cursor: pointer;
  color: red
}
@media (max-width: 800px) {
.hideonmobile {
    display: none;
}
}
@media (min-width: 800px) {
.hideondesktop {
    display: none;
}
}















































































































