.selectfile {
  border-radius: 5px;
  background-color: lightslategrey;
}

.navul {
  list-style: none;
  border-bottom-style: solid;
  border-bottom-color: dimgrey;
}
.navitem {
  color: darkblue;
  text-decoration: none;
  margin-right: 12px;
}
.navitem:hover {
  color: aqua;
  text-decoration: none;
}
.navlogout {
  color: darkred;
  float: left;
}
.navlogoutitem {
  color: darkred;
  text-decoration: none;
}
.navlogoutitem:hover {
  color: red;
  text-decoration: none;
}
.navforum {
  color: black;
  text-decoration: none;
  margin-left: 10px;
}
.navforum:hover {
  color: whitesmoke;
  text-decoration: none;
}
.nopeimageinnavbar{
  margin-bottom: 5px;
  width: 20px;
  height: 20px;
}


.playericonscontainer{
    margin-left: 10px;
    position: relative;
}
.playerbutton{
    border-radius: 15px;
    position: relative; /*neu*/
    float: left;
    width: 45px;
    height: 45px;
}
.playericon{
    width: 100%;
    height: 100%;
}
.playericonoverlay{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
}
.playericon:hover {
    cursor: pointer;
    background-color: lightgrey;
}
.playericonoverlay:hover {
    cursor:pointer;
    opacity: 50%;
}
.nopebutton{
    border-radius: 25px;
    position: relative; /*neu*/
    float: left;
    /*margin: 10px;*/
    width: 150px;
    height: 130px;
    margin: 10px;
}
.nopeimage{/*neu*/
    display: block;
    width: 100%;
    height: 100%;
}
.nopeoverlay{/*neu*/
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    display: block;
    width: 100%;
    height: 100%;
}
.nopebutton:hover {
    cursor:-webkit-grab;
    cursor:grab;
    /*background-color: lightgrey;*/
}
.nopeoverlay:hover {
    cursor:-webkit-grab;
    cursor:grab;
    opacity: 20%;
}
.nowplayingtextdiv {
    float: left;
    font-size: 14px;
    margin-left: 10px;
}
.nowplayingtitlediv {
    float: left;
    font-size: 18px;
    margin-left: 10px;
}
.nowplayingchanneldiv {
    float: left;
    font-size: 14px;
    margin-left: 10px;
}
.chatwindow{
    border-radius: 10px;
    border-style: solid;
    border-color: black;
    margin-left: 10%;
    margin-right: 10%;
    background-color: cadetblue
}
.chat {
    border-radius: 5px;
    text-align: left;
    margin-left: 10px;
    background-color: whitesmoke;
    margin: 5px;
    border-style: solid;
    border-width: 2px;
}
.chatuser{
    margin-left: 3px;
    color: darkblue;
    display: inline;
}
.chatmessage{
    display: inline;
    margin-left: 5px;
}
.newcommentcontainer{
    margin-top: 10px;
}
.newcommentinput {
    width: 60%;
    border-radius: 5px;
    border-style: solid;
    border-color: black;
}
.newcommentsendbutton{
    border-radius: 5px;
    border-style: solid;
    border-color: black;
    background-color: cadetblue;
    color: whitesmoke;
}
@media (max-width: 800px) {
.playericonscontainer{
    margin-left: 10%;
}
.playerbutton{
    width: 30px;
    height: 30px;
}
.nopebutton{
    margin-left: 10%;
    margin-right: 20%;
    width: 40%;
    height: 100px;
}
.nowplayingtextdiv {
    clear:both;
}
.nowplayingtitlediv {
    clear:both;
}
.nowplayingchanneldiv {
    clear:both;
}
}













